import React from 'react';
import { Menu, Icon, Input, Button } from 'semantic-ui-react';

export default () => {
  return (
    <Menu style={{ marginTop: '10px'}} size='medium'>
      <Menu.Item>
        <Icon name='home' />
        Chatroom
      </Menu.Item>

      <Menu.Item>
        <Icon name='github' />
        Code
      </Menu.Item>

      <Menu.Item>
        <Input icon="pencil" placeholder="username" />
        <Button
          content='Change username'
          icon='pencil'
        />
      </Menu.Item>

      <Menu.Item position="right">
        <Icon name='question circle' />
        About
      </Menu.Item>
    </Menu>
  )
}
